import request from '../utils/request'
import { apiCache } from '../utils/apiCache'

function searchMusic(keywords) {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/search',
      params: {
        keywords,
        t: Date.now()
      }
    })
  }, `/search`)
}

function getSearchHot() {
  return apiCache(() => {
    return request({
      method: 'get',
      url: '/search/hot',
      params: {
        t: Date.now()
      }
    })
  }, `/search`)
}

export { getSearchHot, searchMusic }
